#container{
    position: absolute;
    width: 100%;
    height: 100vh;
    overflow:hidden;
}
/* Styling navigation menu    */
.navbar{
    position: absolute;
    display: flex;
    width: 100%;
    height: 45px;
    justify-content: center;
}

#nav{
    position: absolute;
    top: 6px;
}

#nav ul{
    display: flex;
    width: 100%;

}

#nav ul li{
    position: relative;
    list-style: none;
    margin-top: 7px;
    margin-inline: 12px;
    font-size: 15px;
    font-family: 'Lato', sans-serif;
    width: 100%;
}
#nav ul li a{
    text-decoration: none;
    letter-spacing: 0.5px;
    color: #a0a0a0;
    
}

/* animation of side bar navigation menu links      */
@-webkit-keyframes navlinkfade{
    from{
        opacity: 0;
        -webkit-transform: translateX(-200px);
                transform: translateX(-200px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}
@keyframes navlinkfade{
    from{
        opacity: 0;
        -webkit-transform: translateX(-200px);
                transform: translateX(-200px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

/* creating animated cyan color underline on navigation links    */
#nav ul li a:after{
    content: "";
    position: absolute;
    background-color: #00FFFF;
    height: 0.5px;
    width: 0;
    left: 0;
    top: 20px;
    transition: 0.3s;
    z-index: 4;
}
#nav ul li a:hover{
    color: #FFFFFF;
}

#nav ul li a:hover:after{
    width:100%;
}

.nvg{
    color: #FFFFFF!important;
}

.nvg:after{
    content: ""!important;
    position: absolute!important;
    background-color: #00FFFF!important;
    height: 0.5px!important;
    width: 100%!important;
    left: 0!important;
    top: 20px!important;
}

